// src/components/schedule/ConfigTabs.tsx
'use client';

import React, { useState } from 'react';
import {
  Tabs,
  TabsContent,
  TabsList,
  TabsTrigger,
} from '@/components/ui/tabs';
import {
    Card,
    CardContent,
    CardDescription,
    CardFooter,
    CardHeader,
    CardTitle,
} from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { ScrollArea } from '@/components/ui/scroll-area';
import { AlertDialog, AlertDialogTrigger, AlertDialogContent, AlertDialogHeader, AlertDialogTitle, AlertDialogDescription, AlertDialogFooter, AlertDialogCancel, AlertDialogAction } from '@/components/ui/alert-dialog';
import { Switch } from '@/components/ui/switch';
import { Label } from '@/components/ui/label';
import { PlusCircle, Edit, Trash2, Copy } from 'lucide-react';
import type { Location, Department, Employee, ScheduleTemplate } from '@/types/schedule'; // Import types

interface ConfigTabsProps {
  locations: Location[];
  departments: Department[];
  employees: Employee[];
  savedTemplates: ScheduleTemplate[];
  selectedLocationId?: string;
  onOpenLocationModal: (location?: Location | null) => void;
  onDeleteLocation: (locationId: string) => void;
  onOpenDepartmentModal: (department?: Department | null) => void;
  onDeleteDepartment: (departmentId: string) => void;
  onOpenEmployeeModal: (employee?: Employee | null) => void;
  onDeleteEmployee: (employeeId: string) => void;
  onLoadTemplate: (templateId: string) => void;
  onDeleteTemplate: (templateId: string) => void;
  onCopyEmployeeId: (id: string) => void;
  handleSaveEmployee: (employeeData: { name: string; id: string; locationIds: string[] }) => void;
  setEmployeeFormData: React.Dispatch<React.SetStateAction<{ name: string; id: string; locationIds: string[] }>>;
  employeeFormData: { name: string; id: string; locationIds: string[] };
  handleEmployeeLocationChange: (locationId: string) => void;
  editingEmployee: Employee | null;
}

export const ConfigTabs: React.FC<ConfigTabsProps> = ({
  locations,
  departments,
  employees,
  savedTemplates,
  selectedLocationId,
  onOpenLocationModal,
  onDeleteLocation,
  onOpenDepartmentModal,
  onDeleteDepartment,
  onOpenEmployeeModal,
  onDeleteEmployee,
  onLoadTemplate,
  onDeleteTemplate,
  onCopyEmployeeId,
  handleSaveEmployee,
  setEmployeeFormData,
  employeeFormData,
  handleEmployeeLocationChange,
  editingEmployee
}) => {
  const [searchTerm, setSearchTerm] = useState('');
  const [locationToDelete, setLocationToDelete] = useState<string | null>(null);
  const [departmentToDelete, setDepartmentToDelete] = useState<string | null>(null);
  const [employeeToDelete, setEmployeeToDelete] = useState<string | null>(null);

  const filterItems = <T extends { name: string }>(items: T[]): T[] => {
    if (!searchTerm) return items;
    return items.filter(item =>
      item.name.toLowerCase().includes(searchTerm.toLowerCase())
    );
  };

  const filteredLocations = filterItems(locations);
  const filteredDepartments = filterItems(departments.filter(d => !selectedLocationId || d.locationId === selectedLocationId));
  const filteredEmployees = filterItems(employees);
  const filteredTemplates = filterItems(savedTemplates); // Assuming templates have a 'name'

  const confirmDeleteLocation = (id: string) => {
      setLocationToDelete(null); // Close any previous dialog
      onDeleteLocation(id);
  };
  const confirmDeleteDepartment = (id: string) => {
      setDepartmentToDelete(null);
      onDeleteDepartment(id);
  };
 const confirmDeleteEmployee = (id: string) => {
     setEmployeeToDelete(null);
     onDeleteEmployee(id);
 };


  const renderList = <T extends { id: string; name: string }>(
    items: T[],
    onEdit: (item: T) => void,
    onConfirmDelete: (id: string) => void,
    itemType: 'location' | 'department' | 'employee' | 'template'
  ) => (
    <ScrollArea className="h-[calc(100vh-230px)] border rounded-md p-2"> {/* Adjust height */}
      {items.length > 0 ? (
        <ul className="space-y-1 text-sm">
          {items.map((item) => (
            <li key={item.id} className="flex items-center justify-between group py-1 border-b hover:bg-accent rounded-md px-2">
              <span className="truncate mr-2">{item.name}</span>
              <div className="flex items-center gap-1 flex-shrink-0 opacity-0 group-hover:opacity-100 transition-opacity">
                <Button variant="ghost" size="icon" className="h-6 w-6" onClick={() => onEdit(item)}>
                  <Edit className="h-3.5 w-3.5" />
                </Button>
                <AlertDialog>
                     <AlertDialogTrigger asChild>
                         <Button variant="ghost" size="icon" className="h-6 w-6 text-destructive" onClick={(e) => { e.stopPropagation(); /* Prevent edit click */ }}>
                            <Trash2 className="h-3.5 w-3.5" />
                         </Button>
                     </AlertDialogTrigger>
                     <AlertDialogContent>
                         <AlertDialogHeader>
                             <AlertDialogTitle>Confirmar Eliminacin</AlertDialogTitle>
                             <AlertDialogDescription>
                                 Ests seguro de que quieres eliminar "{item.name}"? Esta accin no se puede deshacer.
                                  {itemType === 'location' && ' Se eliminarn tambin los departamentos asociados.'}
                                  {itemType === 'employee' && ' Se eliminarn tambin los turnos asignados a este colaborador.'}
                             </AlertDialogDescription>
                         </AlertDialogHeader>
                         <AlertDialogFooter>
                             <AlertDialogCancel>Cancelar</AlertDialogCancel>
                             <AlertDialogAction className="bg-destructive hover:bg-destructive/90" onClick={() => onConfirmDelete(item.id)}>
                                 Eliminar
                             </AlertDialogAction>
                         </AlertDialogFooter>
                     </AlertDialogContent>
                </AlertDialog>
                {itemType === 'employee' && (
                    <Button variant="ghost" size="icon" className="h-6 w-6" title="Copiar ID" onClick={() => onCopyEmployeeId((item as Employee).id)}>
                        <Copy className="h-3.5 w-3.5" />
                    </Button>
                )}
              </div>
            </li>
          ))}
        </ul>
      ) : (
        <p className="text-center text-sm text-muted-foreground p-4">No hay {itemType}s.</p>
      )}
    </ScrollArea>
  );

  return (
    <Tabs defaultValue="sedes" className="w-full flex-grow flex flex-col overflow-hidden">
      <TabsList className="grid w-full grid-cols-4 shrink-0 mx-4 mt-4 mb-2">
        <TabsTrigger value="sedes">Sedes</TabsTrigger>
        <TabsTrigger value="departamentos">Departamentos</TabsTrigger>
        <TabsTrigger value="colaboradores">Colaboradores</TabsTrigger>
        <TabsTrigger value="templates">Templates</TabsTrigger>
      </TabsList>

       <div className="flex items-center gap-2 shrink-0 px-4 pb-4 border-b">
             <Input
                placeholder="Buscar..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="flex-grow"
            />
            <Button
                 variant="outline"
                 size="icon"
                 onClick={() => {
                     // Trigger the appropriate modal based on the active tab
                     const activeTab = document.querySelector('[data-state="active"]')?.getAttribute('data-value');
                     if (activeTab === 'sedes') onOpenLocationModal();
                     else if (activeTab === 'departamentos') onOpenDepartmentModal();
                     else if (activeTab === 'colaboradores') onOpenEmployeeModal();
                     // Add logic for templates if needed
                 }}
                 title="Agregar Nuevo"
            >
                <PlusCircle className="h-4 w-4" />
            </Button>
       </div>


       <div className="flex-grow overflow-hidden px-4 pt-4">
         <TabsContent value="sedes" className="mt-0 h-full">
            {renderList(filteredLocations, onOpenLocationModal, confirmDeleteLocation, 'location')}
          </TabsContent>
          <TabsContent value="departamentos" className="mt-0 h-full">
            {renderList(filteredDepartments, onOpenDepartmentModal, confirmDeleteDepartment, 'department')}
          </TabsContent>
          <TabsContent value="colaboradores" className="mt-0 h-full">
            {renderList(filteredEmployees, onOpenEmployeeModal, confirmDeleteEmployee, 'employee')}
          </TabsContent>
          <TabsContent value="templates" className="mt-0 h-full">
            {/* Placeholder for template list rendering */}
             <ScrollArea className="h-[calc(100vh-230px)] border rounded-md p-2">
                {/* Use ScheduleTemplateList component here */}
                <ScheduleTemplateList
                     templates={filteredTemplates}
                     onLoadTemplate={onLoadTemplate}
                     onDeleteTemplate={onDeleteTemplate}
                 />
                {/* {filteredTemplates.length > 0 ? (
                   <ul className="space-y-1 text-sm">...</ul>
                ) : (
                   <p className="text-center text-sm text-muted-foreground p-4">No hay templates.</p>
                )} */}
             </ScrollArea>
          </TabsContent>
       </div>
    </Tabs>
  );
};

// Dummy ScheduleTemplateList component - replace with your actual implementation if it exists elsewhere
const ScheduleTemplateList: React.FC<{templates: ScheduleTemplate[], onLoadTemplate: (id: string) => void, onDeleteTemplate: (id: string) => void}> = ({ templates, onLoadTemplate, onDeleteTemplate }) => {
    if (templates.length === 0) {
        return <p className="text-center text-sm text-muted-foreground p-4">No hay templates.</p>;
    }
    return (
        <ul className="space-y-1 text-sm">
            {templates.map(template => (
                 <li key={template.id} className="flex items-center justify-between group py-1 border-b hover:bg-accent rounded-md px-2">
                     <span className="truncate mr-2">{template.name}</span>
                     <div className="flex items-center gap-1 flex-shrink-0 opacity-0 group-hover:opacity-100 transition-opacity">
                         <Button variant="ghost" size="icon" className="h-6 w-6" title="Cargar" onClick={() => onLoadTemplate(template.id)}>
                             {/* Replace with appropriate Load icon */}
                             <svg xmlns="http://www.w3.org/2000/svg" className="h-3.5 w-3.5" viewBox="0 0 20 20" fill="currentColor"><path fillRule="evenodd" d="M3 17a1 1 0 011-1h12a1 1 0 110 2H4a1 1 0 01-1-1zm3.293-7.707a1 1 0 011.414 0L9 10.586V3a1 1 0 112 0v7.586l1.293-1.293a1 1 0 111.414 1.414l-3 3a1 1 0 01-1.414 0l-3-3a1 1 0 010-1.414z" clipRule="evenodd" /></svg>
                         </Button>
                         <AlertDialog>
                            <AlertDialogTrigger asChild>
                                <Button variant="ghost" size="icon" className="h-6 w-6 text-destructive" onClick={(e) => e.stopPropagation()}>
                                    <Trash2 className="h-3.5 w-3.5" />
                                </Button>
                            </AlertDialogTrigger>
                            <AlertDialogContent>
                                <AlertDialogHeader>
                                    <AlertDialogTitle>Confirmar Eliminacin</AlertDialogTitle>
                                    <AlertDialogDescription>
                                        Ests seguro de que quieres eliminar el template "{template.name}"?
                                    </AlertDialogDescription>
                                </AlertDialogHeader>
                                <AlertDialogFooter>
                                    <AlertDialogCancel>Cancelar</AlertDialogCancel>
                                    <AlertDialogAction className="bg-destructive hover:bg-destructive/90" onClick={() => onDeleteTemplate(template.id)}>
                                        Eliminar
                                    </AlertDialogAction>
                                </AlertDialogFooter>
                            </AlertDialogContent>
                         </AlertDialog>
                     </div>
                 </li>
            ))}
        </ul>
    );
};
