// src/components/schedule/SummaryDashboard.tsx
'use client';

import React from 'react';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table';
import type { Employee, ScheduleData, ShiftAssignment } from '@/types/schedule';
import { format } from 'date-fns';
import { es } from 'date-fns/locale';

interface SummaryDashboardProps {
  scheduleData: { [dateKey: string]: ScheduleData };
  employees: Employee[];
  weekDates: Date[];
  viewMode: 'day' | 'week';
  currentDate: Date;
  calculateShiftDuration: (assignment: ShiftAssignment, shiftDate: Date) => number;
}

export const SummaryDashboard: React.FC<SummaryDashboardProps> = ({
  scheduleData,
  employees,
  weekDates,
  viewMode,
  currentDate,
  calculateShiftDuration,
}) => {
  const calculateHours = () => {
    const employeeHours: { [key: string]: { name: string; totalHours: number } } = {};

    const datesToConsider = viewMode === 'week' ? weekDates : [currentDate];

    datesToConsider.forEach(date => {
      const dateKey = format(date, 'yyyy-MM-dd');
      const daySchedule = scheduleData[dateKey];

      if (daySchedule?.assignments) {
        Object.values(daySchedule.assignments).forEach(deptAssignments => {
          deptAssignments.forEach(assignment => {
            if (!employeeHours[assignment.employee.id]) {
              employeeHours[assignment.employee.id] = { name: assignment.employee.name, totalHours: 0 };
            }
            const duration = calculateShiftDuration(assignment, date);
            employeeHours[assignment.employee.id].totalHours += duration;
          });
        });
      }
    });

    // Find the corresponding full employee object for sorting and display
    const summaryList = Object.entries(employeeHours).map(([id, data]) => ({
        id,
        ...data
    })).sort((a,b) => a.name.localeCompare(b.name)); // Sort by name

    return summaryList;
  };

  const summary = calculateHours();
  const totalHoursAllEmployees = summary.reduce((sum, emp) => sum + emp.totalHours, 0);

  return (
    <div className="space-y-4">
      {summary.length > 0 ? (
        <Table>
          <TableHeader>
            <TableRow>
              <TableHead>Colaborador</TableHead>
              <TableHead className="text-right">Horas</TableHead>
            </TableRow>
          </TableHeader>
          <TableBody>
            {summary.map((emp) => (
              <TableRow key={emp.id}>
                <TableCell className="font-medium truncate">{emp.name}</TableCell>
                <TableCell className="text-right">{emp.totalHours.toFixed(1)}</TableCell>
              </TableRow>
            ))}
            {/* Total Row */}
             <TableRow className="border-t">
                 <TableCell className="font-bold">Total</TableCell>
                 <TableCell className="text-right font-bold">{totalHoursAllEmployees.toFixed(1)}</TableCell>
             </TableRow>
          </TableBody>
        </Table>
      ) : (
        <p className="text-center text-muted-foreground italic">
          No hay horas asignadas para {viewMode === 'week' ? 'esta semana' : 'este da'}.
        </p>
      )}
    </div>
  );
};
