// --- Constants for Local Storage Keys ---
const LOCATIONS_KEY = 'schedulePlannerLocations';
const DEPARTMENTS_KEY = 'schedulePlannerDepartments';
const EMPLOYEES_KEY = 'schedulePlannerEmployees';
const SCHEDULE_DATA_KEY = 'schedulePlannerData'; // Key for daily/weekly schedule data
const SCHEDULE_NOTES_KEY = 'schedulePlannerNotes'; // New key for notes
const SCHEDULE_TEMPLATES_KEY = 'schedulePlannerTemplates'; // New key for templates
const SCHEDULE_FOOTER_NOTES_KEY = 'schedulePlannerFooterNotes'; // New key for footer notes

// --- Helper Function to Load from localStorage ---
const loadFromLocalStorage = <T,>(key: string, defaultValue: T): T => {
    if (typeof window === 'undefined') {
        return defaultValue; // Return default during SSR
    }
    try {
        const savedData = localStorage.getItem(key);
        if (!savedData) return defaultValue;
        const parsed = JSON.parse(savedData);
        // Basic validation if needed (e.g., check if it's an array or object)
        if (typeof defaultValue === 'object' && defaultValue !== null) {
            if (Array.isArray(defaultValue) && !Array.isArray(parsed)) return defaultValue;
            if (!Array.isArray(defaultValue) && typeof parsed !== 'object') return defaultValue;
        }
        return parsed;
    } catch (error) {
        console.error(`Error loading ${key} from localStorage:`, error);
        return defaultValue;
    }
};

// Nueva funcin para cargar templates
const loadScheduleTemplates = (): ScheduleTemplate[] => {
    if (typeof window === 'undefined') return []; // Solo en cliente

    const loadedTemplates: ScheduleTemplate[] = [];
    // Assume templates might be stored individually or under a main key
    const templatesMainKey = SCHEDULE_TEMPLATES_KEY; // Use the constant
    const templatePrefix = "tpl-"; // For individually stored templates (legacy or alternative)

    try {
        // Try loading from the main key first
        const mainStoredData = localStorage.getItem(templatesMainKey);
        if (mainStoredData) {
             try {
                const parsedArray = JSON.parse(mainStoredData);
                if (Array.isArray(parsedArray)) {
                    parsedArray.forEach((templateData: any) => {
                        // Basic validation
                        if (templateData && typeof templateData === 'object' && templateData.id && templateData.name) {
                           if (templateData.createdAt && typeof templateData.createdAt === 'string') {
                               try { templateData.createdAt = parseISO(templateData.createdAt); } catch {}
                           }
                           loadedTemplates.push(templateData as ScheduleTemplate);
                        }
                    });
                    console.log(`[loadScheduleTemplates] Loaded ${loadedTemplates.length} templates from main key '${templatesMainKey}'.`);
                    loadedTemplates.sort((a, b) => (a.name || '').localeCompare(b.name || ''));
                    return loadedTemplates; // Return if loaded successfully from main key
                }
             } catch (parseError) {
                 console.error(`Error parsing templates from main key '${templatesMainKey}':`, parseError);
             }
        }

        // Fallback: Try loading individually stored templates if main key didn't yield results
        console.log("[loadScheduleTemplates] Main key not found or invalid, trying individual keys...");
        for (let i = 0; i < localStorage.length; i++) {
            const key = localStorage.key(i);
            if (key && key.startsWith(templatePrefix)) {
                const storedData = localStorage.getItem(key);
                if (storedData) {
                    try {
                        const parsedTemplate = JSON.parse(storedData) as ScheduleTemplate;
                        if (parsedTemplate && typeof parsedTemplate === 'object' && parsedTemplate.id && parsedTemplate.name) {
                           if (parsedTemplate.createdAt && typeof parsedTemplate.createdAt === 'string') {
                               try { parsedTemplate.createdAt = parseISO(parsedTemplate.createdAt); } catch {}
                           }
                           // Avoid duplicates if somehow present in both storage methods
                           if (!loadedTemplates.some(t => t.id === parsedTemplate.id)) {
                                loadedTemplates.push(parsedTemplate);
                           }
                        } else {
                             console.warn(`Invalid data found for template key: ${key}`);
                        }
                    } catch (parseError) {
                        console.error(`Error parsing JSON for template key ${key}:`, parseError);
                    }
                }
            }
        }
    } catch (error) {
        console.error("Error loading templates from localStorage:", error);
    }

    loadedTemplates.sort((a, b) => (a.name || '').localeCompare(b.name || ''));
    console.log(`[loadScheduleTemplates] Loaded ${loadedTemplates.length} templates (individual keys or combined).`);
    return loadedTemplates;
};


