    // --- Schedule Manipulation Handlers ---
    const handleSaveSchedule = () => {
        if (isClient) {
            try {
                localStorage.setItem(SCHEDULE_DATA_KEY, JSON.stringify(scheduleData));
                toast({ title: "Horario Guardado", description: "La planificacin actual ha sido guardada localmente." });
            } catch (error) {
                 console.error("Error saving schedule to localStorage:", error);
                 toast({ title: "Error al Guardar", description: "No se pudo guardar el horario.", variant: "destructive" });
            }
        }
    };

    const handleDuplicateDay = (sourceDate: Date) => {
        const targetDate = addDays(sourceDate, 1);
        const sourceDateKey = format(sourceDate, 'yyyy-MM-dd');
        const targetDateKey = format(targetDate, 'yyyy-MM-dd');

        if (!scheduleData[sourceDateKey]) {
            toast({ title: "Nada que Duplicar", description: `No hay horario definido para el ${format(sourceDate, 'PPP', { locale: es })}.`, variant: "default" });
            return;
        }

        // Deep copy assignments, generating new IDs for each assignment
        const copiedAssignments: { [deptId: string]: ShiftAssignment[] } = {};
        const sourceAssignments = scheduleData[sourceDateKey].assignments || {};
        for (const deptId in sourceAssignments) {
            copiedAssignments[deptId] = sourceAssignments[deptId].map(assign => ({
                ...assign,
                id: `shift_${uuidv4()}` // Generate a new unique ID
            }));
        }

        setScheduleData(prev => ({
            ...prev,
            [targetDateKey]: {
                date: targetDate,
                assignments: copiedAssignments
            }
        }));
        toast({ title: "Horario Duplicado", description: `El horario del ${format(sourceDate, 'dd/MM')} se copi al ${format(targetDate, 'dd/MM/yyyy')}.` });
         // Optionally, move the view to the next day
         setCurrentDate(targetDate);
         if(viewMode === 'week') {
             // If duplicating the last day of the week view, move to the next week
            const weekEnd = endOfWeek(sourceDate, { weekStartsOn: 1 });
             if (isSameDay(sourceDate, weekEnd)) {
                 handleNext(); // Navigate to the next week
             }
         }
    };

    // Handler to duplicate the entire week's schedule to the next week
     const handleDuplicateWeek = () => {
         const nextWeekStart = addWeeks(startOfWeek(currentDate, { weekStartsOn: 1 }), 1);
         let assignmentsCopiedCount = 0;

         setScheduleData(prev => {
             const newSchedule = { ...prev };
             weekDates.forEach((sourceDate, index) => {
                 const sourceDateKey = format(sourceDate, 'yyyy-MM-dd');
                 const targetDate = addDays(nextWeekStart, index);
                 const targetDateKey = format(targetDate, 'yyyy-MM-dd');

                 if (prev[sourceDateKey]?.assignments && Object.keys(prev[sourceDateKey].assignments).length > 0) {
                     // Deep copy with new IDs
                     const copiedAssignments: { [deptId: string]: ShiftAssignment[] } = {};
                     const sourceAssignments = prev[sourceDateKey].assignments;
                     for (const deptId in sourceAssignments) {
                         copiedAssignments[deptId] = sourceAssignments[deptId].map(assign => ({
                             ...assign,
                             id: `shift_${uuidv4()}`
                         }));
                          assignmentsCopiedCount += copiedAssignments[deptId].length;
                     }
                     newSchedule[targetDateKey] = {
                         date: targetDate,
                         assignments: copiedAssignments
                     };
                 } else {
                      // If source day is empty, ensure target day is also empty or removed
                      if (newSchedule[targetDateKey]) {
                         // Optionally clear it: delete newSchedule[targetDateKey];
                         // Or set empty assignments: newSchedule[targetDateKey] = { date: targetDate, assignments: {} };
                      }
                 }
             });
             return newSchedule;
         });

         if (assignmentsCopiedCount > 0) {
             toast({ title: "Semana Duplicada", description: `Se copiaron ${assignmentsCopiedCount} turnos a la semana del ${format(nextWeekStart, 'dd/MM')}.` });
             setCurrentDate(nextWeekStart); // Navigate to the duplicated week
         } else {
             toast({ title: "Semana Vaca", description: "La semana actual no tena turnos para duplicar.", variant: "default" });
         }
     };


    const handleClearDay = (dateToClear: Date) => {
        const dateKey = format(dateToClear, 'yyyy-MM-dd');
         let shiftsCleared = 0;
         let notesCleared = 0;

        setScheduleData(prev => {
            if (!prev[dateKey]) return prev; // No data to clear
             const daySchedule = prev[dateKey];
             shiftsCleared = Object.values(daySchedule.assignments || {}).reduce((sum, deptAssigns) => sum + deptAssigns.length, 0);

            const newSchedule = { ...prev };
            // Create a new object for the day with empty assignments
            newSchedule[dateKey] = { date: dateToClear, assignments: {} };
            return newSchedule;
        });

         // Also clear notes for that day
         setScheduleNotes(prevNotes => {
             const notesBefore = prevNotes.length;
             const updatedNotes = prevNotes.filter(note => note.date !== dateKey);
             notesCleared = notesBefore - updatedNotes.length;
             return updatedNotes;
         });


         if (shiftsCleared > 0 || notesCleared > 0) {
            toast({ title: "Da Limpiado", description: `Se eliminaron ${shiftsCleared} turnos y ${notesCleared} anotaciones del ${format(dateToClear, 'PPP', { locale: es })}.`, variant: "destructive" });
         } else {
             toast({ title: "Da Vaco", description: `No haba turnos ni anotaciones para eliminar el ${format(dateToClear, 'PPP', { locale: es })}.`, variant: "default" });
         }
    };

     // Handler to clear the entire week's schedule
     const handleClearWeek = () => {
         let shiftsCleared = 0;
         let notesCleared = 0;
         const weekDateKeys = weekDates.map(date => format(date, 'yyyy-MM-dd'));

         setScheduleData(prev => {
             const newSchedule = { ...prev };
             let changed = false;
             weekDateKeys.forEach(dateKey => {
                 if (newSchedule[dateKey]?.assignments && Object.keys(newSchedule[dateKey].assignments).length > 0) {
                     shiftsCleared += Object.values(newSchedule[dateKey].assignments).reduce((sum, deptAssigns) => sum + deptAssigns.length, 0);
                     newSchedule[dateKey] = { date: parseDateFns(dateKey, 'yyyy-MM-dd', new Date()), assignments: {} }; // Keep date object, clear assignments
                     changed = true;
                 }
             });
             return changed ? newSchedule : prev;
         });

          // Also clear notes for the week
          setScheduleNotes(prevNotes => {
              const notesBefore = prevNotes.length;
              const updatedNotes = prevNotes.filter(note => !weekDateKeys.includes(note.date));
              notesCleared = notesBefore - updatedNotes.length;
              return updatedNotes.length !== notesBefore ? updatedNotes : prevNotes;
          });


         if (shiftsCleared > 0 || notesCleared > 0) {
             toast({ title: "Semana Limpiada", description: `Se eliminaron ${shiftsCleared} turnos y ${notesCleared} anotaciones de la semana actual.`, variant: "destructive" });
         } else {
             toast({ title: "Semana Vaca", description: "No haba turnos ni anotaciones para eliminar en esta semana.", variant: "default" });
         }
     };


    // --- Holiday Check Function ---
    const isHoliday = useCallback((date: Date | null | undefined): boolean => {
        if (!date || !isValid(date)) return false;
        const dateStr = format(date, 'yyyy-MM-dd');
        return holidays.has(dateStr);
    }, [holidays]);

    // --- Notes Handlers ---
     const handleAddNote = (newNoteData: Omit<ScheduleNote, 'id'>) => {
         const newNote: ScheduleNote = {
             ...newNoteData,
             id: `note-${uuidv4()}`
         };
         setScheduleNotes(prev => [...prev, newNote].sort((a, b) => a.date.localeCompare(b.date))); // Keep sorted
         toast({ title: "Anotacin Guardada", description: `Nota para el ${format(parseDateFns(newNote.date, 'yyyy-MM-dd', new Date()), 'PPP', { locale: es })} aadida.` });
     };

     const handleDeleteNote = (noteId: string) => {
         const noteToDelete = scheduleNotes.find(n => n.id === noteId);
         setScheduleNotes(prev => prev.filter(note => note.id !== noteId));
         if (noteToDelete) {
             toast({ title: "Anotacin Eliminada", description: `Nota "${noteToDelete.note}" eliminada.`, variant: "destructive" });
         }
         setNoteToDeleteId(null); // Reset confirmation state
     };

    const getNotesForDate = useCallback((date: Date): ScheduleNote[] => {
        const dateKey = format(date, 'yyyy-MM-dd');
        return scheduleNotes.filter(note => note.date === dateKey);
    }, [scheduleNotes]);

    // Opens the notes modal, optionally focusing on a specific date
    const handleOpenNotesModal = (date?: Date) => {
        setNotesModalDate(date); // If date is provided, modal will focus on it
        setIsNotesModalOpen(true);
    };

    // --- Template Handlers ---

     const handleSaveTemplate = (type: 'day' | 'week') => {
         if (!selectedLocationId) {
             toast({ title: "Error", description: "Selecciona una sede primero.", variant: "destructive" });
             return;
         }

         const templateName = prompt(`Ingresa un nombre para el template ${type === 'day' ? 'diario' : 'semanal'}:`);
         if (!templateName || !templateName.trim()) {
             toast({ title: "Cancelado", description: "No se ingres un nombre.", variant: "default" });
             return;
         }

         let assignmentsToSave: DailyAssignments | WeeklyAssignments = {};
         let daysCount = 0;

         if (type === 'day') {
             const dateKey = format(currentDate, 'yyyy-MM-dd');
             const daySchedule = scheduleData[dateKey];
             if (!daySchedule || Object.keys(daySchedule.assignments || {}).length === 0) {
                 toast({ title: "Error", description: "No hay horario definido para este da.", variant: "destructive" });
                 return;
             }
              const dailyAssignments: DailyAssignments = {};
              for(const deptId in daySchedule.assignments) {
                  dailyAssignments[deptId] = daySchedule.assignments[deptId].map(({ id, employee, ...rest }) => ({ ...rest, employee: { id: employee.id } })); // Store only employee ID
              }
              assignmentsToSave = dailyAssignments;
              daysCount = 1;

         } else { // week
             const weeklyAssignments: WeeklyAssignments = {};
             let weekHasAssignments = false;
             weekDates.forEach(date => {
                 const dateKey = format(date, 'yyyy-MM-dd');
                 const daySchedule = scheduleData[dateKey];
                  if (daySchedule?.assignments && Object.keys(daySchedule.assignments).length > 0) {
                      const dailyAssignmentsForTemplate: DailyAssignments = {};
                       for(const deptId in daySchedule.assignments) {
                           dailyAssignmentsForTemplate[deptId] = daySchedule.assignments[deptId].map(({ id, employee, ...rest }) => ({ ...rest, employee: { id: employee.id } }));
                       }
                       weeklyAssignments[dateKey] = dailyAssignmentsForTemplate;
                       weekHasAssignments = true;
                  }
             });
             if (!weekHasAssignments) {
                  toast({ title: "Error", description: "No hay horario definido para esta semana.", variant: "destructive" });
                  return;
             }
             assignmentsToSave = weeklyAssignments;
             daysCount = Object.keys(weeklyAssignments).length;
         }

         const newTemplate: ScheduleTemplate = {
             id: `tpl-${uuidv4()}`,
             name: templateName.trim(),
             locationId: selectedLocationId,
             type: type,
             assignments: assignmentsToSave,
             createdAt: new Date() // Add creation timestamp
         };

         setSavedTemplates(prev => {
             const updatedTemplates = [...prev, newTemplate];
             updatedTemplates.sort((a, b) => (a.name || '').localeCompare(b.name || ''));
             console.log("[Save Template] New template added:", newTemplate);
             console.log("[Save Template] All templates now:", updatedTemplates);
             return updatedTemplates;
         });

         toast({ title: "Template Guardado", description: `Template "${newTemplate.name}" (${type}, ${daysCount} da/s) guardado.` });
     };

   // Function to apply a loaded template to the current view
    const applyTemplateToSchedule = useCallback((template: ScheduleTemplate) => {
        console.log("[Apply Template] Applying template:", template);
        console.log("[Apply Template] Current viewMode:", viewMode);
        console.log("[Apply Template] Template type:", template.type);

        if (!selectedLocationId) {
             toast({ title: "Error", description: "Selecciona una sede primero.", variant: "destructive" });
             return;
        }
        // Ensure template location matches selected location (or allow global templates)
        if (template.locationId && template.locationId !== selectedLocationId) {
            toast({ title: "Error de Sede", description: `El template "${template.name}" es para otra sede.`, variant: "destructive", duration: 6000 });
            return;
        }
        // Ensure template type matches current view mode
         if (viewMode !== template.type) {
             toast({ title: "Error de Tipo", description: `El template "${template.name}" es ${template.type}. Cambia a la vista ${template.type} para cargarlo.`, variant: "destructive", duration: 6000 });
             return;
         }


        if (viewMode === 'day') { // Apply daily template to current day
            const dateKey = format(currentDate, 'yyyy-MM-dd');
            const templateAssignments = template.assignments as DailyAssignments;
            const dayAssignments: { [deptId: string]: ShiftAssignment[] } = {};
            let shiftsAppliedCount = 0;

            for (const deptId in templateAssignments) {
                 // Use ALL departments for potential mapping, not just filtered ones initially
                 const department = departments.find(d => d.id === deptId);
                 if (!department || department.locationId !== selectedLocationId) continue; // Skip if department doesn't exist or isn't in current location

                dayAssignments[deptId] = templateAssignments[deptId].map(tempAssign => {
                    const employee = employees.find(e => e.id === tempAssign.employee.id);
                    // Ensure employee exists AND is associated with the current location
                    if (!employee || !employee.locationIds.includes(selectedLocationId)) return null;
                    shiftsAppliedCount++;
                    return {
                        ...tempAssign,
                        id: `shift_${uuidv4()}`,
                        employee: employee // Use full employee object
                    };
                }).filter((a): a is ShiftAssignment => a !== null); // Filter out nulls and type guard
            }


            setScheduleData(prev => ({
                ...prev,
                [dateKey]: {
                    date: currentDate,
                    assignments: dayAssignments
                }
            }));
            toast({ title: "Template Diario Aplicado", description: `Template "${template.name}" aplicado al ${format(currentDate, 'PPP', { locale: es })} (${shiftsAppliedCount} turnos).` });

        } else { // Apply weekly template to current week (viewMode === 'week')
            const templateAssignments = template.assignments as WeeklyAssignments;
            let shiftsAppliedCount = 0;

            setScheduleData(prev => {
                const newSchedule = { ...prev };
                weekDates.forEach((date) => {
                    const dateKey = format(date, 'yyyy-MM-dd');
                    // Template data uses dateKeys directly from when it was saved
                    const templateDayAssignments = templateAssignments[dateKey]; // Find template data for this SPECIFIC date
                    const dayAssignmentsForSchedule: { [deptId: string]: ShiftAssignment[] } = {};

                    if (templateDayAssignments) {
                        for (const deptId in templateDayAssignments) {
                             // Use ALL departments, then filter by current location
                             const department = departments.find(d => d.id === deptId);
                             if (!department || department.locationId !== selectedLocationId) continue;

                            dayAssignmentsForSchedule[deptId] = templateDayAssignments[deptId].map(tempAssign => {
                                const employee = employees.find(e => e.id === tempAssign.employee.id);
                                // Ensure employee exists AND is associated with the current location
                                if (!employee || !employee.locationIds.includes(selectedLocationId)) return null;
                                shiftsAppliedCount++;
                                return {
                                    ...tempAssign,
                                    id: `shift_${uuidv4()}`,
                                    employee: employee
                                };
                            }).filter((a): a is ShiftAssignment => a !== null);
                        }
                    }
                    // Always update the schedule for the day, even if template data was missing (clears the day)
                    newSchedule[dateKey] = {
                        date: date,
                        assignments: dayAssignmentsForSchedule
                    };
                });
                return newSchedule;
            });

            if (shiftsAppliedCount > 0) {
                toast({ title: "Template Semanal Aplicado", description: `Template "${template.name}" aplicado a la semana actual (${shiftsAppliedCount} turnos).` });
            } else {
                 toast({ title: "Template Vaco o Incompleto", description: `El template "${template.name}" no contena asignaciones vlidas para esta semana/ubicacin.`, variant:"default" });
            }
        }
    }, [currentDate, viewMode, weekDates, setScheduleData, employees, departments, selectedLocationId, toast]);


