      // Helper function to calculate shift duration in hours (decimal)
     const calculateShiftDuration = (assignment: ShiftAssignment, shiftDate: Date): number => {
          try {
              const start = parseDateFns(assignment.startTime, 'HH:mm', shiftDate);
              let end = parseDateFns(assignment.endTime, 'HH:mm', shiftDate);

              if (isNaN(start.getTime()) || isNaN(end.getTime())) return 0;


              if (end < start) {
                  end = addDays(end, 1); // Handle overnight shifts
              }

              let diffMinutes = differenceInMinutes(end, start);

              if (assignment.includeBreak && assignment.breakStartTime && assignment.breakEndTime) {
                   const breakStartValid = isValid(parseDateFns(assignment.breakStartTime, 'HH:mm', shiftDate));
                   const breakEndValid = isValid(parseDateFns(assignment.breakEndTime, 'HH:mm', shiftDate));

                  if(breakStartValid && breakEndValid) {
                      const breakStart = parseDateFns(assignment.breakStartTime, 'HH:mm', shiftDate);
                      let breakEnd = parseDateFns(assignment.breakEndTime, 'HH:mm', shiftDate);

                      // Basic handling if break crosses midnight (relative to shift start) - adjust if needed
                      const breakStartMinutes = breakStart.getHours() * 60 + breakStart.getMinutes();
                      const shiftStartMinutes = start.getHours() * 60 + start.getMinutes();
                      let breakDate = shiftDate;
                      if (breakStartMinutes < shiftStartMinutes) {
                          breakDate = addDays(shiftDate, 1); // Assume break starts next day if time is earlier
                      }
                      const breakStartCorrected = parseDateFns(assignment.breakStartTime, 'HH:mm', breakDate);
                      let breakEndCorrected = parseDateFns(assignment.breakEndTime, 'HH:mm', breakDate);


                      if (breakEndCorrected < breakStartCorrected) {
                           breakEndCorrected = addDays(breakEndCorrected, 1); // Handle overnight break
                      }

                       // Ensure break times are within the shift duration for accurate deduction
                       const actualBreakStart = breakStartCorrected > start ? breakStartCorrected : start;
                       const actualBreakEnd = breakEndCorrected < end ? breakEndCorrected : end;

                       if (actualBreakEnd > actualBreakStart) {
                          const breakDiff = differenceInMinutes(actualBreakEnd, actualBreakStart);
                          diffMinutes -= breakDiff;
                       }
                  }
              }

              return Math.max(0, diffMinutes / 60); // Return hours, ensure non-negative
          } catch (error) {
              console.error("Error calculating duration for assignment:", assignment, error);
              return 0; // Return 0 on error
          }
     };


